import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    AppGlobals.initialize(mapOf("title" to "Portfolio"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.example.portfolio.pages.HomePage() }
        ctx.router.register("/demo") { org.example.portfolio.pages.Demo() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("banner", org.example.portfolio.styles.bannerStyle)
        ctx.theme.registerStyle("gradient-tag-line",
                org.example.portfolio.styles.GradientTagLineStyle)
        ctx.theme.registerStyle("button", org.example.portfolio.styles.buttonStyle)
        ctx.theme.registerStyle("contact", org.example.portfolio.styles.ContactStyle)
        ctx.theme.registerStyle("form", org.example.portfolio.styles.formStyle)
        ctx.theme.registerStyle("footer", org.example.portfolio.styles.FooterStyle)
        ctx.theme.registerStyle("news-letter-box", org.example.portfolio.styles.NewsLetterBoxStyle)
        ctx.theme.registerStyle("new-email-box", org.example.portfolio.styles.NewEmailBoxStyle)
        ctx.theme.registerStyle("nav-bar", org.example.portfolio.styles.NavBarStyle)
        ctx.theme.registerStyle("nav-bar-button", org.example.portfolio.styles.NavBarButtonStyle)
        ctx.theme.registerStyle("nav-bar-link", org.example.portfolio.styles.NavBarLinkStyle)
        ctx.theme.registerStyle("paragraph", org.example.portfolio.styles.ParagraphStyle)
        ctx.theme.registerStyle("project-card-img-box",
                org.example.portfolio.styles.projectCardImgBox)
        ctx.theme.registerStyle("project-card-text-overlay",
                org.example.portfolio.styles.ProjectCardTextOverlay)
        ctx.theme.registerKeyframes("zoom-in", org.example.portfolio.styles.zoomIn)
        ctx.theme.registerKeyframes("up-down", org.example.portfolio.styles.upDownAnim)
        org.example.portfolio.updateTheme(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        org.example.portfolio.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
